<template>
  <v-container
    fluid
    class="fill-height"
    style="background: linear-gradient(90deg, #0072ff 0%, #00d4ff 100%);"
  >
    <v-card class="mx-auto" shaped width="26rem" max-width="94%">
      <v-card-text class="text-center">
        <v-avatar size="128" class="elevation-3">
          <img :src="$store.getters['ficha/avatar']" alt="" style="object-fit: cover">
        </v-avatar>
        <p class="title mb-0 mt-2" style="line-height: 1rem">{{nome}}</p>
        <p class="blue--text subtitle-2 mb-2">Corretor de Imóveis</p>
        <v-text-field
          v-if="telefone"
          label="Telefone Comercial"
          :value="telefone"
          v-mask="'(##) #########'"
          readonly
        ></v-text-field>
        <v-text-field
          v-if="email"
          label="E-mail principal"
          :value="email"
          readonly
        ></v-text-field>
        <v-btn
          fab
          x-small
          color="primary"
          style="position: absolute; right: .3rem; top: .3rem;"
          to="/"
        >
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
  export default {
    name: 'Perfil',
    computed: {
      nome() {
        return this.$store.state.ficha.cadastro ? this.$store.state.ficha.cadastro.nome : '';
      },
      telefone() {
        return this.$store.state.ficha.cadastro ? this.$store.state.ficha.cadastro.telefone : '';
      },
      email() {
        return this.$store.state.ficha.cadastro ? this.$store.state.ficha.cadastro.email : '';
      },
    },
  }
</script>
